"use client";

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Badge } from '@/components/ui/badge';
import Link from 'next/link';
import ErrorBoundary from '@/components/ErrorBoundary';

export default function AdminPage() {
  const [formData, setFormData] = useState({
    type: '',
    source: '',
    headline: '',
    content: '',
    page: '',
    editoria: '',
    mediaType: '',
    duration: '',
    author: ''
  });
  
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState<string | null>(null);
  const [error, setError] = useState<string | null>(null);

  const handleInputChange = (field: string, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError(null);
    setSuccess(null);

    try {
      const response = await fetch('/api/clippings', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(formData),
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.message || 'Erro ao cadastrar clipping');
      }

      setSuccess('Clipping cadastrado com sucesso!');
      
      // Reset form
      setFormData({
        type: '',
        source: '',
        headline: '',
        content: '',
        page: '',
        editoria: '',
        mediaType: '',
        duration: '',
        author: ''
      });
      
    } catch (err) {
      console.error('Error creating clipping:', err);
      setError(err instanceof Error ? err.message : 'Erro desconhecido ao cadastrar clipping');
    } finally {
      setLoading(false);
    }
  };

  const renderFormFields = () => {
    switch (formData.type) {
      case 'print':
        return (
          <>
            <div className="grid grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label htmlFor="page">Página *</Label>
                <Input
                  id="page"
                  type="number"
                  placeholder="Ex: 3"
                  value={formData.page}
                  onChange={(e) => handleInputChange('page', e.target.value)}
                  required
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="editoria">Editoria *</Label>
                <Input
                  id="editoria"
                  placeholder="Ex: Política Local"
                  value={formData.editoria}
                  onChange={(e) => handleInputChange('editoria', e.target.value)}
                  required
                />
              </div>
            </div>
            <div className="space-y-2">
              <Label htmlFor="content">Conteúdo HTML *</Label>
              <Textarea
                id="content"
                placeholder="Digite o conteúdo da matéria em HTML..."
                value={formData.content}
                onChange={(e) => handleInputChange('content', e.target.value)}
                rows={4}
                required
              />
            </div>
          </>
        );
      
      case 'electronic':
        return (
          <>
            <div className="grid grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label htmlFor="mediaType">Tipo de Mídia *</Label>
                <Select value={formData.mediaType} onValueChange={(value) => handleInputChange('mediaType', value)}>
                  <SelectTrigger>
                    <SelectValue placeholder="Selecione o tipo" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="radio">Rádio</SelectItem>
                    <SelectItem value="tv">TV</SelectItem>
                  </SelectContent>
                </Select>
              </div>
              <div className="space-y-2">
                <Label htmlFor="duration">Duração</Label>
                <Input
                  id="duration"
                  placeholder="Ex: 8:45"
                  value={formData.duration}
                  onChange={(e) => handleInputChange('duration', e.target.value)}
                />
              </div>
            </div>
          </>
        );
      
      case 'digital':
        return (
          <>
            <div className="space-y-2">
              <Label htmlFor="author">Autor</Label>
              <Input
                id="author"
                placeholder="Nome do autor da matéria"
                value={formData.author}
                onChange={(e) => handleInputChange('author', e.target.value)}
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="content">Conteúdo da Matéria *</Label>
              <Textarea
                id="content"
                placeholder="Digite o conteúdo completo da matéria..."
                value={formData.content}
                onChange={(e) => handleInputChange('content', e.target.value)}
                rows={6}
                required
              />
            </div>
          </>
        );
      
      default:
        return (
          <div className="text-center py-8 text-muted-foreground">
            Selecione um tipo de mídia para continuar
          </div>
        );
    }
  };

  return (
    <ErrorBoundary>
      <div className="container mx-auto py-8 space-y-8">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div className="space-y-2">
          <h1 className="text-3xl font-bold tracking-tight">
            Painel Administrativo
          </h1>
          <p className="text-muted-foreground">
            Cadastre novas matérias jornalísticas no sistema de clipping
          </p>
        </div>
        <Button asChild variant="outline">
          <Link href="/clipping-dashboard">
            Ver Dashboard
          </Link>
        </Button>
      </div>

      {/* Success/Error Messages */}
      {success && (
        <Alert>
          <AlertDescription>{success}</AlertDescription>
        </Alert>
      )}
      
      {error && (
        <Alert variant="destructive">
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      )}

      <Tabs defaultValue="cadastro" className="space-y-4">
        <TabsList>
          <TabsTrigger value="cadastro">Cadastrar Notícia</TabsTrigger>
          <TabsTrigger value="instrucoes">Instruções</TabsTrigger>
        </TabsList>

        <TabsContent value="cadastro">
          <Card>
            <CardHeader>
              <CardTitle>Cadastrar Nova Matéria</CardTitle>
              <CardDescription>
                Preencha os campos abaixo para adicionar uma nova matéria ao sistema de clipping
              </CardDescription>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleSubmit} className="space-y-6">
                {/* Tipo de Mídia */}
                <div className="space-y-2">
                  <Label htmlFor="type">Tipo de Mídia *</Label>
                  <Select value={formData.type} onValueChange={(value) => handleInputChange('type', value)}>
                    <SelectTrigger>
                      <SelectValue placeholder="Selecione o tipo de mídia" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="print">
                        <div className="flex items-center gap-2">
                          <Badge variant="secondary">Impressa</Badge>
                          <span>Jornais e Revistas</span>
                        </div>
                      </SelectItem>
                      <SelectItem value="electronic">
                        <div className="flex items-center gap-2">
                          <Badge variant="default">Eletrônica</Badge>
                          <span>Rádio e TV</span>
                        </div>
                      </SelectItem>
                      <SelectItem value="digital">
                        <div className="flex items-center gap-2">
                          <Badge variant="outline">Digital</Badge>
                          <span>Sites e Blogs</span>
                        </div>
                      </SelectItem>
                    </SelectContent>
                  </Select>
                </div>

                {/* Campos Comuns */}
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="source">Fonte/Veículo *</Label>
                    <Input
                      id="source"
                      placeholder="Ex: Jornal VS, Rádio São Leopoldo FM"
                      value={formData.source}
                      onChange={(e) => handleInputChange('source', e.target.value)}
                      required
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="headline">Manchete/Título *</Label>
                    <Input
                      id="headline"
                      placeholder="Título da matéria"
                      value={formData.headline}
                      onChange={(e) => handleInputChange('headline', e.target.value)}
                      required
                    />
                  </div>
                </div>

                {/* Campos Específicos por Tipo */}
                {renderFormFields()}

                {/* Submit Button */}
                <div className="flex justify-end space-x-2">
                  <Button
                    type="button"
                    variant="outline"
                    onClick={() => {
                      setFormData({
                        type: '',
                        source: '',
                        headline: '',
                        content: '',
                        page: '',
                        editoria: '',
                        mediaType: '',
                        duration: '',
                        author: ''
                      });
                      setError(null);
                      setSuccess(null);
                    }}
                  >
                    Limpar
                  </Button>
                  <Button type="submit" disabled={loading || !formData.type}>
                    {loading ? 'Cadastrando...' : 'Cadastrar Matéria'}
                  </Button>
                </div>
              </form>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="instrucoes">
          <Card>
            <CardHeader>
              <CardTitle>Instruções de Uso</CardTitle>
              <CardDescription>
                Como cadastrar matérias no sistema de clipping
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-6">
              <div className="space-y-4">
                <div>
                  <h3 className="font-semibold mb-2 flex items-center gap-2">
                    <Badge variant="secondary">Mídia Impressa</Badge>
                  </h3>
                  <ul className="text-sm text-muted-foreground space-y-1 ml-4">
                    <li>• Fonte: Nome do jornal ou revista</li>
                    <li>• Página: Número da página onde foi publicada</li>
                    <li>• Editoria: Seção do jornal (ex: Política, Cidade, Economia)</li>
                    <li>• Conteúdo: Texto da matéria em formato HTML</li>
                  </ul>
                </div>

                <div>
                  <h3 className="font-semibold mb-2 flex items-center gap-2">
                    <Badge variant="default">Mídia Eletrônica</Badge>
                  </h3>
                  <ul className="text-sm text-muted-foreground space-y-1 ml-4">
                    <li>• Fonte: Nome da rádio ou TV</li>
                    <li>• Tipo: Selecione entre Rádio ou TV</li>
                    <li>• Duração: Tempo de duração da matéria (ex: 8:45)</li>
                    <li>• Manchete: Título ou descrição da matéria</li>
                  </ul>
                </div>

                <div>
                  <h3 className="font-semibold mb-2 flex items-center gap-2">
                    <Badge variant="outline">Mídia Digital</Badge>
                  </h3>
                  <ul className="text-sm text-muted-foreground space-y-1 ml-4">
                    <li>• Fonte: Nome do site, portal ou blog</li>
                    <li>• Autor: Nome do jornalista ou autor (opcional)</li>
                    <li>• Conteúdo: Texto completo da matéria</li>
                    <li>• Manchete: Título da matéria</li>
                  </ul>
                </div>
              </div>

              <div className="bg-muted p-4 rounded-lg">
                <h4 className="font-semibold mb-2">📝 Dicas Importantes:</h4>
                <ul className="text-sm space-y-1">
                  <li>• Todos os campos marcados com * são obrigatórios</li>
                  <li>• As matérias aparecerão automaticamente no dashboard</li>
                  <li>• Você pode incluir as matérias nas newsletters</li>
                  <li>• O sistema gera automaticamente a data de inserção</li>
                </ul>
              </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
      </div>
    </ErrorBoundary>
  );
}
